/*Booking*/

.booking-container {
    margin: 1rem auto;
    width: 75%;
}

.booking-header {
    border-top: 0.1rem solid #ccc;
}

.booking-row {
    border-bottom: 0.1rem solid #ccc;
    display: -ms-flexbox;
    display: flex;
}

.booking-field {
    border-right: 0.1rem solid #ccc;
    -ms-flex: 1 1;
        flex: 1 1;
    padding: 0.5rem 0;
}

.booking-field:first-child {
    border-left: 0.1rem solid #ccc;
}



